import { useEffect, useRef, useState } from "react";
import Chip8 from "./Chip";

const Canvas = ({ file, keyPressed, setKey }) => {
   const [ch, setCh] = useState();
   const [beep, setBeep] = useState();
   const canvasRef = useRef(null);

   useEffect(() => {
      const canvas = canvasRef.current;
      const cntx = canvas.getContext('2d');
      cntx.fillStyle = '#FFFFFF';
      cntx.fillRect(0, 0, cntx.canvas.width, cntx.canvas.height);
      setBeep(new Audio("data:audio/wav;base64,"));  
   },[]);

   useEffect(() => {
      if(file.length > Chip8.MAX_MEM)
         return;
      else
         setCh(new Chip8(file));

      return () => {
         setCh(null);
      }
   }, [file]);
   
   useEffect(() => {
      document.addEventListener('keydown', (e) => setKey(e.code));

      return () => {
         document.removeEventListener('keydown', () => setKey(null));
      }
   },);

   useEffect(() => {
      const interval = setInterval(() => {
         ch.run(keyPressed, canvasRef.current.getContext('2d'));
      }, 2);   
      
      return () => clearInterval(interval);
   }, [ch, keyPressed]);

   useEffect(() => {
      const intervalTimer = setInterval(() => {
         if(ch.dTimer > 0) 
            ch.dTimer -= 1;
         
         if(ch.sTimer > 0) {
            ch.sTimer -= 1;
            beep.play();
         }

         if(ch.display)
            ch.render(canvasRef.current.getContext('2d'));
      }, 17);   
      
      return () => clearInterval(intervalTimer);
   }, [ch, beep]);

   return(
      <>
         <canvas ref = {canvasRef} />
      </>
   );
}

export default Canvas;